<template>
  <div>
    <b-sidebar id="sidebar-right" title="Sidebar" right shadow>
      <div class="px-3 py-2">
        <ul class="list-group">
          <li class="list-group-item">
            <ul class="list-group">
              <li
                v-for="(details, item, i) in cart"
                class="list-group-item"
                :key="i"
              >
                <div class="row mp-0">
                  Item: {{ item }}, Price: {{ details.p | displayPrice }}
                </div>
                <div class="row mp-0">
                  Quantity: {{details.q}}
                </div>
                <b-button variant="success" @click="addItem(item)">Add</b-button>
                <b-button variant="danger" @click="deleteItem(item)"
                  >Delete</b-button
                >
              </li>
            </ul>
          </li>
          <li class="list-group-item text-right">Taxes: {{taxes | displayPrice}}</li>

          <li class="list-group-item text-right">
            <strong>Total: {{total | displayPrice}}</strong>
          </li>
        </ul>
      </div>
    </b-sidebar>
  </div>
</template>

<script>
export default {
  props: {},
  computed: {
    cart() {
      console.log("Cart:", this.$store.getters.getCart)
      return this.$store.getters.getCart;
    },
    taxes() {
      return .15*this.total
    },
    total() {

      var temp = this.cart
      var total = 0
      for (var c in temp) {
        console.log(temp[c].q, temp[c].p)
        total += temp[c].q * temp[c].p
      }
      // console.log(temp, total)
      return total




    }
  },
  methods: {
    deleteItem(item) {
      console.log(item, this.cart[item], "Deleting");
      this.$store.commit("removeItem", item);
    },
    addItem(item) {
      console.log(item, this.cart[item], "Adding");
      let temp = {}
      temp[item] = {}
      this.$store.commit("updateCart", temp)
    }
  },
  components: {},
  filters: {
    displayPrice(num) {
      console.log(num)
      return "$" + num.toFixed(2);
    },
  },
};
</script>

